<template>
  <section id="credential-section">
    <h1 class="section-title">자격증 및 수료증</h1>
    <div>
      <ul class="box-wrap" ref="box_animation">
        <li class="box" v-for="(item, index) in credentialList" :key="index" ref="boxItem">
          <div class="contents">
            <p class="title">{{ item.title }}</p>
            <img :src="item.img" />
            <div class="description">
              <p class="date">{{ item.date }}</p>
              <p class="sub-title">{{ item.subTitle }}</p>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <div class="scroll"></div>
  </section>
</template>

<script>
import { ref, onMounted, onUnmounted } from 'vue';
import computer_img from '@/assets/images/credential/computer.jpg';
import design_img from '@/assets/images/credential/design.jpg';
import dental_img from '@/assets/images/credential/dental.jpg';
import icdl_img from '@/assets/images/credential/icdl.jpg';

export default {
  setup() {
    const credentialList = [
      {
        title: '웹디자인 기능사',
        subTitle: '한국산업인력공단',
        date: '2021.09.24',
        img: computer_img,
      },
      {
        title: '스마트웹&UI/UX 퍼블리싱 전문가',
        subTitle: '더조은아카데미',
        date: '2019.08~2020.01',
        img: design_img,
      },
      {
        title: '치과 위생사',
        subTitle: '보건복지부',
        date: '2016.02.25',
        img: dental_img,
      },
      {
        title: 'ICDL',
        subTitle: '한국생산성본부',
        date: '2013.11.01',
        img: icdl_img,
      },
    ];

    const boxItem = ref();
    const box_animation = ref();

    let isDown = false;
    let progress = 50;
    let startX = 0;
    let active = 0;

    const speedWheel = 0.2;
    const speedDrag = -0.1;

    const getZindex = (array, index) => (array.map((_, i) => (index === i) ? array.length : array.length - Math.abs(index - i)));
    
    onMounted(() => {
      // wheel 이벤트 추가
      document.addEventListener('mousewheel', handleWheel);
      document.addEventListener('mousedown', handleMouseDown);
      document.addEventListener('mousemove', handleMouseMove);
      document.addEventListener('mouseup', handleMouseUp);
      document.addEventListener('touchstart', handleMouseDown);
      document.addEventListener('touchmove', handleMouseMove);
      document.addEventListener('touchend', handleMouseUp);
      
      setTimeout(() => {
        box_animation.value.style.opacity = 1;
      }, 200);

      animate();

      boxItem.value.forEach((item, i) => {
        item.addEventListener('click', () => {
          progress = (i/boxItem.value.length) * 100 + 30
          animate();
        })
      });
    })

    onUnmounted(() => {
      document.removeEventListener('mousewheel', handleWheel);
      document.removeEventListener('mousedown', handleMouseDown);
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
      document.removeEventListener('touchstart', handleMouseDown);
      document.removeEventListener('touchmove', handleMouseMove);
      document.removeEventListener('touchend', handleMouseUp);
    });

    const displayItems = (item, index, active) => {
      const zIndex = getZindex([...boxItem.value], active)[index];
      item.style.setProperty('--zIndex', zIndex);
      item.style.setProperty('--active', (index-active)/boxItem.value.length);
    }

    const animate = () => {
      progress = Math.max(0, Math.min(progress, 100));
      active = Math.floor(progress/100*(boxItem.value.length-1));
      boxItem.value.forEach((item, index) => displayItems(item, index, active));
    }

    const handleWheel = e => {
      console.log(boxItem.value[boxItem.value.length-1].style[--active],e.deltaY === 100)
      if(boxItem.value&&boxItem.value[boxItem.value.length-1].style&&boxItem.value[boxItem.value.length-1].style[--active] === '--active' && e.deltaY === 100) window.open('#/skill','_parent');
      const wheelProgress = e.deltaY * speedWheel
      progress = progress + wheelProgress
      animate();
    };

    const handleMouseMove = (e) => {
      if (!isDown) return
      const x = e.clientX || (e.touches && e.touches[0].clientX) || 0
      const mouseProgress = (x - startX) * speedDrag
      progress = progress + mouseProgress
      startX = x
      animate();
    };

    const handleMouseDown = e => {
      isDown = true;
      startX = e.clientX || (e.touches && e.touches[0].clientX) || 0;
    }

    const handleMouseUp = () => {
      isDown = false;
    }
    
    return {
      credentialList,
      boxItem,
      box_animation,
    };
  }
}
</script>